import Col from 'react-bootstrap/Col';
import Accordion from 'react-bootstrap/Accordion';
import {
  ResearchContainer,
  ResearchHeader,
  ResearchCompany,
  ResearchBodyContainer,
  ResearchTitle,
  ResearchDescriptionItem,
} from './styles';
import data from '../../data';

const Research = () => {
  const { research } = data;
  return (
    <ResearchContainer>
      <Col md={8} xs={12}>
        <Accordion.Item eventKey='2'>
          <Accordion.Header>
            <ResearchHeader>{research.header}</ResearchHeader>
          </Accordion.Header>
          <Accordion.Body>
            {research.body.map((researcher) => (
              <ResearchBodyContainer>
                <ResearchCompany href={researcher.link} target='_blank'>
                  {researcher.company}
                </ResearchCompany>
                <ResearchTitle>{researcher.title}</ResearchTitle>
                  {<ul>
                  {researcher.description.map((text) => (
                    <ResearchDescriptionItem>{text}</ResearchDescriptionItem>
                  ))}
                </ul> }
              </ResearchBodyContainer>
            ))}
          </Accordion.Body>
        </Accordion.Item>
      </Col>
    </ResearchContainer>
  );
};

export default Research;
