import Col from 'react-bootstrap/Col';
import Carousel from 'react-bootstrap/Carousel';
import Accordion from 'react-bootstrap/Accordion';

import {
  ProjectsContainer2,
  ProjectsHeader2,
  CarouselWrapper2,
  ProjectTitle2,
  ProjectContainer2,
  ProjectDescription2,
} from './styles';
import data from '../../data';

const Project2 = ({ length, project2, index }) => (
  <ProjectContainer2>
    <Col md={10} xs={12}>
      <ProjectTitle2>{project2.title}</ProjectTitle2>
      <CarouselWrapper2>
        <Carousel interval={5000}>
          {project2.pictures.map((pic, index) => (
            <Carousel.Item>
              <img className='w-100' src={pic} alt={`${index} slide`} width="500" height={"600"}/>
            </Carousel.Item>
          ))}
        </Carousel>
      </CarouselWrapper2>
      <ProjectDescription2>{project2.description}</ProjectDescription2>
      {index < length - 1 ? <hr className='project-divider' /> : null}
    </Col>
  </ProjectContainer2>
);

const Projects2 = () => {
  const { projects2 } = data;
  const { body } = projects2;
  return (
    <ProjectsContainer2>
      <Col md={8} xs={12}>
        <Accordion.Item eventKey='4'>
          <Accordion.Header>
            <ProjectsHeader2>{projects2.header}</ProjectsHeader2>
          </Accordion.Header>
          <Accordion.Body>
            {body.map((project2, index) => (
              <Project2 length={body.length} project2={project2} index={index} />
            ))}
          </Accordion.Body>
        </Accordion.Item>
      </Col>
    </ProjectsContainer2>
  );
};

export default Projects2;
